<script>
import { Field, Header, Button, TabContainer, TabContainerItem, Cell, Toast, Popup, MessageBox, Spinner } from 'mint-ui';

export default {
	name: 'Order',
	data (){
		return {
			flag: false,
			selected: '1', //
			isShowMore: false,//显示更多
			isUnscRollable: false, //是否页面不可滚动
			backParentPage: false, //是否回到父页面
			isNothing: false,//是否显示无数据
			pid: this.$route.query.pid,//传递的页面ID
			orderList: [], //订单列表
			curOl: '',//当前选中的订单
			curOrderList: '', //物流查询 当前订单
			limit: 10,//每页显示条数
			page: 1,//当前页数
			noMore: false,//是否没有更多
		}
	},
	components: {
		'mt-field': Field,
		'mt-header': Header,
		'mt-button': Button,
	    'mt-tab-container': TabContainer,
	    'mt-tab-container-item': TabContainerItem,
    	'mt-cell': Cell,
    	'mt-popup': Popup,
    	'mt-spinner': Spinner,
	},
	watch: {
		selected (){
			var curSelected = this.selected; 

			// 1.在线未支付    2.货到付款待确认    3.待发货    4.待收货    5.物流待收款    6.财务待收款    7.已完成    8.已取消    9.支付中
			this.isNothing = false;
			//切换状态时先清空数据
			this.orderList = [];
			//当前页改为默认1
			this.page = 1;

			if( curSelected == 1 ){
				this.$options.methods.getAllList( this ); //全部订单
			}else if( curSelected == 2 ){
				this.$options.methods.getList( this, JSON.stringify([1,9]) ); //待付款
			}else if( curSelected == 3 ){
				this.$options.methods.getList( this, JSON.stringify([2,3]) ); //待发货
			}else if( curSelected == 4 ){
				this.$options.methods.getList( this, JSON.stringify([4]) ); //待收货
			}else if( curSelected == 5 ){
				this.$options.methods.getList( this, JSON.stringify([5,6,7]) ); //已完成
			}else if( curSelected == 6 ){
				this.$options.methods.getList( this, JSON.stringify([8]) ); //已取消
			}
		},
	},
	filters: {
		//订单状态过滤
		orderStatusFilter ( data ){
			// 1.在线未支付    2.货到付款待确认    3.待发货    4.待收货    5.物流待收款    6.财务待收款    7.已完成    8.已取消    9.支付中
			if( data == 1 || data == 9 ){ return '待付款' }
			else if( data == 3 || data == 2 ){ return '待发货' }
			else if( data == 4 ){ return '待收货' }
			else if( data == 5 || data == 6 || data == 7 ){ return '已完成' }
			// else if( data == 5 || data == 6 ){ return '已收货' }
			else if( data == 8 ){ return '已取消' }
		},
	},
	mounted (){
		const _self = this;

		this.$emit('changeClass',false);
	},
	updated (){
	    // this.$nextTick(function () {
	    //   if( !this.backParentPage ){
	    //     this.isUnscRollable = true;
	    //   }
	    // }) 
		const _self = this;

		this.$nextTick(()=>{
			var box = document.getElementById('orderItems');
			var boxHeight = box.offsetHeight;
			var itemCon = _self.$refs.itemCon;
			var itemConHeight = itemCon.$el.offsetHeight;
			var winHeight = document.documentElement.clientHeight || document.body.clientHeight;

			// console.log('itemCon: ', itemCon.$el.offsetHeight)
	  		// 监听这个dom的scroll事件
			box.addEventListener('scroll', () => {
				// console.log('scroll: ', itemCon.$el.offsetHeight-box.scrollTop, boxHeight );
				// console.log('scroll: ', itemCon.$el.offsetHeight );
				//滚动高度
				var boxScrollTop = box.scrollTop;
				console.log('12: ', itemConHeight, boxScrollTop, boxHeight );

				if( _self.noMore ){ return false; }

				if( boxHeight >= (itemConHeight - boxScrollTop - 100) ){
					console.log('3333333333', itemCon.$el.offsetHeight-box.scrollTop-100)
					_self.noMore = true;
					//显示加载动画
					_self.$refs.loadMore.style.visibility = 'visible';
					console.log(_self.$refs.loadMore.style.visibility )

					setTimeout(function(){
						//加载下一页
						_self.page++;
						// _self.$options.methods.getAllList( _self ); //全部订单
						
						if( _self.selected == 1 ){
							_self.$options.methods.getAllList( _self ); //全部订单
						}else if( _self.selected == 2 ){
							_self.$options.methods.getList( _self, JSON.stringify([1,9]) ); //待付款
						}else if( _self.selected == 3 ){
							_self.$options.methods.getList( _self, JSON.stringify([2,3]) ); //待发货
						}else if( _self.selected == 4 ){
							_self.$options.methods.getList( _self, JSON.stringify([4]) ); //待收货
						}else if( _self.selected == 5 ){
							_self.$options.methods.getList( _self, JSON.stringify([5,6,7]) ); //已完成
						}else if( _self.selected == 6 ){
							_self.$options.methods.getList( _self, JSON.stringify([8]) ); //已取消
						}
					},300);
				}
			}, false)
		});
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	created (){
		const _self = this;
		if( this.pid ){
			this.selected = String(this.pid);
			// this.$options.methods.getList( this, this.pid );
			return false;
		}

		this.$options.methods.getAllList( this );
	},
	methods: {
	    //切换tab
	    tabClick(index){ 
	      this.selected = index;
	    },
	    //跳转物流详情
	    jumpExpress ( data ){
	    	// console.log('订单详情：', data)
	    	this.$store.commit('saveOrderInfo', data);
	    	this.$router.push({ path: '/order/express'});
	    	// this.$router.push({ path: '/myinfo/express'});
	    },
	    //跳转订单详情
	    jumpDetail ( data, index ){
	    	// console.log(data)
	    	// return 
	    	// this.curOrderList = data;
	    	this.curOl = index;//当前选中的订单
	    	this.$store.commit('saveOrderInfo', data);

	    	this.$router.push({ path: '/order/order_detail'});
	    	// this.$router.push({ path: '/myinfo/order_detail', query: {'staus': staus}});
	    	// console.log(this.$store.state.orderInfo)
	    },
	    //确认收货
	    takeDelivery ( item ){
	    	const _self = this;

	    	if( this.flag ){return false}
    		MessageBox.confirm('确定执行此操作?').then(action => {
		    	_self.flag = true;	
		    	_self.$ajax({
		    		url: _self.webRoot + '/order/updateOrderStatusByConfirmReceipt',
		    		data: {
		    			orderId: item.orderID
		    		}
		    	})
		    	.then(function(res){
		    		// console.log(res)
		    		if( !res.data.success ){
		    			_self.$options.methods.showToast( res.data.msg );
		    			_self.flag = false;
		    			return false;
		    		}
					_self.$options.methods.showToast( res.data.msg );
					setTimeout(function(){
						_self.$router.go(0);
						_self.flag = false;
					},1000);

		    	})
		    	.catch(function(err){
		    		console.log(err)
		    	})
	    	}).catch(function(cancel){

	    	});
	    },
	    //从子组件更新数据
	    childUpdata ( data ){
	    	var index = this.curOl;
	    	this.orderList[index] = data;
	    	//如果是在代发货页面 把订单移除
	    	if( this.selected == '3' ){
	    		this.orderList.splice(index,1);
	    	}
	    },

	    //公共函数
	    //获取全部订单
	    getAllList ( _self ){
			// _self.orderList = [];
			if( !_self.$store.state.memberId ){
				return false;
			}

			_self.$ajax({
				// url: '/apiserver/order/getOrderByMemberId',
				url: _self.webRoot + '/order/getOrderByMemberId',
				data: {
					memberId: _self.$store.state.memberId,
					page: _self.page,
					limit: _self.limit
				}
			})
			.then(function(res){
				// console.log(res)
				if( res.data.success ){
					if(res.data.data.length == 0){
						if( res.data.count == 0 ){ _self.isNothing = true; }
						_self.$options.methods.showToast('没有更多数据!');
						console.log('没有更多数据!', _self.$refs.loadMore)
						//显示-隐藏加载动画
						_self.$refs.loadMore.style.visibility = 'hidden';
						return false;
					}
					// _self.orderList = res.data.data;
					for( let i=0; i<res.data.data.length; i++ ){
						_self.orderList.push(res.data.data[i]);
					}
					//更改可以加载更多
					_self.noMore = false;
				}else{
					_self.$options.methods.showToast( res.data.msg );
				}
			})
			.catch(function(err){
				console.log(err)
			})
	    },
	    //获取状态订单 /order/getOrderByMemberIdAndStatus
	    getList ( _self, state ){
	    	// console.log('state: ',typeof JSON.parse(state))
			// _self.orderList = [];
			if( !_self.$store.state.memberId ){
				return false;
			}

			_self.$ajax({
				url: _self.webRoot + '/order/getOrderByMemberIdAndStatus',
				// data: {
				// 	memberId: _self.$store.state.memberId,
				// 	status: status 
				// }
				data: JSON.stringify({
					memberId: _self.$store.state.memberId,
					status: JSON.parse(state),
					page: _self.page,
					limit: _self.limit
				}),
				headers: {'Content-Type' : 'application/json;charset=utf-8'},
				transformRequest: [function (data) {
						return data
				}]
			})
			.then(function(res){
				// console.log(res)
				if( res.data.success ){
					if( !res.data.data || res.data.data.length === 0 ){
						// _self.$options.methods.showToast('没有更多数据!');
						// _self.orderList.splice(0);
						// _self.isNothing = true;
						// return false;
						// 
						if( res.data.count == 0 ){ _self.isNothing = true }
						_self.$options.methods.showToast('没有更多数据!');
						//显示-隐藏加载动画
						_self.$refs.loadMore.style.visibility = 'hidden';
						return false;
					}
					// _self.orderList = res.data.data;
					for( let i=0; i<res.data.data.length; i++ ){
						_self.orderList.push(res.data.data[i]);
					}
					//更改可以加载更多
					_self.noMore = false;
					// console.log('dddd: ',_self.orderList)
				}else{
					_self.$options.methods.showToast( res.data.msg );
				}
			})
			.catch(function(err){
				console.log(err)
			})
	    },
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
		//显示更多
		showMore (){
			this.isShowMore = true;
		},
		// 后退
		goBack (){	
			//清除订单信息
			if( this.$store.state.orderInfo ){
				this.$store.commit('saveOrderInfo','');
				sessionStorage.setItem('sesorderinfo', '');
			}
			this.$router.go(-1);
		},
		//确定按钮
		btnSave (){	
			console.log('点击确定')
		},
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
	},
}	
</script>

<style scoped>
@import '../../../static/css/headerTitle.css';
@import './Css/Order.css';	
</style>


